                  {{itm.productName}}
                </div>
                <div class="shopSpecs" v-if="itm.productAttr">
                  <div v-for="(ite,i) in JSON.parse(itm.productAttr || `[]`)" :key="i">{{ite.value}}</div>
                </div>
                <div class="bbottom">
                  <div class="left" v-if="item.payType === 5"><span>{{itm.productPrice}}</span>积分</div>
                  <div class="left" v-else>￥<span>{{itm.productPrice}}</span></div>
