              class="custom-image"
              image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
              description="暂无数据"
            />
          </div>
          <van-loading size="24px" vertical type="spinner" v-if="!isOk && data.length === 0">加载中...</van-loading>
        </van-tab>
        <van-tab title="已完成" name="3">
          <div v-if="data && data.length > 0">
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoading()"
          >
          <div class="orderBody" v-for="item in data" :key="item.id">
            <div class="orderTop">
              <p>订单号：<span>{{item.orderSn}}</span></p>
              <p>{{item | statusFilter}}</p>
            </div>
            <div class="orderCenter" @click="toDetails(item.id)" v-for="itm in item.orderItemList" :key="itm.id">
              <div class="orderimg">
