        </van-tab>
        <van-tab title="已取消" name="4">
          <div v-if="data && data.length > 0">
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoading()"
          >
          <div class="orderBody" v-for="item in data" :key="item.id">
            <div class="orderTop">
              <p>订单号：<span>{{item.orderSn}}</span></p>
              <p>{{item | statusFilter}}</p>
            </div>
            <div class="orderCenter" @click="toDetails(item.id)" v-for="itm in item.orderItemList" :key="itm.id">
              <div class="orderimg">
                <van-image :src="itm.productPic ? itm.productPic:''"></van-image>
              </div>
              <div class="orderfont">
                <div class="ttop">
                  {{itm.productName}}
                </div>
                <div class="shopSpecs" v-if="itm.productAttr">
                  <div v-for="(ite,i) in JSON.parse(itm.productAttr || `[]`)" :key="i">{{ite.value}}</div>
                </div>
                <div class="bbottom">
                  <div class="left" v-if="item.payType === 5"><span>{{itm.productPrice}}</span>积分</div>
                  <div class="left" v-else>￥<span>{{itm.productPrice}}</span></div>
                  <div>x{{itm.productQuantity}}<span class="cancel">{{item | changeStatus(itm)}}</span></div>
                </div>
              </div>
            </div>
