</template>

<script>
import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'
export default {
  components :{
    Header
  },
  data() {
    return {
      isOk:false,
      activeName: "-1",
      show: false,
      columns: [],
      data:[],
      reasonList:[],
      selectBt:'0',
      form: {
          pageNum: 1,
          pageSize: 5,
          total: 0,
          status: '-1'
        },
      allAmount:'',
      deleID:0,
      loading: false,
      finished: false,
      newArr:[],
    };
  },
  filters:{
    changeStatus(item, itm) {
      let font = null
      item.returnApplyList.forEach((term) => {
        if(term.orderItemId === itm.id && term.status !== 3) {
          if(term.type === 1) {
            font = '退货'
          } else if(term.type === 3) {
            font = '换货'
          } else if(term.type === 2) {
            font = '退款'
          }
        }
      })
      return font
    },
    returnApply(item){
      if(item.returnApplyList.length === 0){
        return false
      }else{
        return true
      }
    },
    isCancel(item){
      let state = false
      item.payVoucherList.forEach(item => { 
        if(item.confirm === 0 || item.confirm === 1){
          state = true
        }
      })
      return state
    },
    isDisabled(item){
      let confirm = false
      item.payVoucherList.forEach(item=>{
        if(item.confirm === 0){
          confirm = true
        }
      })
      if(confirm){
        return true
      }else{
        return false
      }
    },
    isConfirm(item){
      let confirm = false
      item.payVoucherList.forEach(item=>{
        if(item.confirm === 0){
          confirm = true
        }
      })
      if(confirm){
        return '审核中'
      }else{
        return '立即付款'
      }
    },
    statusFilter (itm) {
        // 0->待付款；1->待发货；2->已发货；3->已完成；4->已关闭；5->无效订单
        if (itm.status === 0) {
          if (itm.payType === 3 && itm.payVoucher) {
            return '待审核'
          } else {
            return '待付款'
          }
        } else if (itm.status === 1) {
          return '待发货'
        } else if (itm.status === 2) {
          return '待收货'
        } else if (itm.status === 3) {
          return '已完成'
        } else if (itm.status === 4) {
          return '已取消'
        } else if (itm.status === 5) {
          return '无效订单'
        } else if (itm.status === 6) {
          return '未付款待发货'
        } else if (itm.status === 7) {
          return '未付款已发货'
        } else if (itm.status === 8) {
          return '未付款已收货'
        } else if (itm.status === 10) {
          return '退货中'
        } else if (itm.status === 11) {
          return '已退款'
        } else if (itm.status === 12) {
          return '已退货'
        }
    }
  },
  mounted(){
    if (window.history && window.history.pushState) {
      // 向历史记录中插入了当前页
      history.pushState(null, null, document.URL);
      window.addEventListener('popstate', this.goBack, false);
    }
    localStorage.setItem('historyUrl', '')
    this.getactiveName();
    this.showOrder(this.activeName);
    this.getorderReason();
  },
  destroyed () {
    window.removeEventListener('popstate', this.goBack, false);
  },
  methods: {
    goBack () {
      // sessionStorage.clear();
      // window.history.back();
      this.$router.push({
        path:'/user/home'
      })
    },
    showOrder(num){
      let me = this
        me.form.status = num || -1
        me.form.pageNum = 1
        me.loading = false
        me.finished = false
        me.newArr = []
        me.data = []
        me.isOk = false
        api.getOrderList(me.form).then(res => {
          if (res.code === 200) {
            if (res.data.list) {
              res.data.list.forEach((itm) => {
                itm.returnApplyList.forEach((item,index) => {
                  if(item.type === 2 && item.status === 3){
                    itm.returnApplyList.splice(index, 1)
                  }
                });
              });
              me.data = res.data.list
            } else {
              me.data = []
              setTimeout(()=>{
                me.isOk = true
              },500)
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
    },
    onLoading(){
      let me = this
      api.getOrderList(me.form).then(res => {
          if (res.code === 200) {
            if (res.data.list) {
              me.form.pageNum++
              res.data.list.forEach(itm => {
                itm.returnApplyList.forEach((item,index) => {
                  if(item.type === 2 && item.status === 3){
                    itm.returnApplyList.splice(index, 1)
                  }
                });
              });
              me.newArr = me.newArr.concat(res.data.list)
              me.data = me.newArr
              if(me.data.length >= res.data.total){
                me.loading = true
                me.finished = true
              }else{
                me.loading = false
              }
            } else {
              me.data = []
            }
          } else {
            me.$message.error(res.message);
          }
        }).catch((res) => {
          me.$message.error(res.message);
        }).finally(() => {
        })
    },
    getactiveName(){
      this.activeName = this.$route.query.toorders;
    },
    showPopup(id) {
      this.deleID = id
      this.show = true;
    },
    onConfirm(value) {
      let me = this
      let subform = {
        'id' : me.deleID,
        'reason' : value,
        'reasonId' : 0
      }
      me.reasonList.forEach((itm)=>{
        if(value === itm.name){
          subform.reasonId = itm.id
        }
      })
        api.cancelUserOrder(subform).then(res => {
          if (res.code === 200) {
            me.$toast.success('取消成功');
            this.show = false;
            me.showOrder(me.activeName)
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      
    },
    getorderReason(){
      let me = this
      api.cancelOrderReason().then(res => {
          if (res.code === 200) {
            res.data.forEach((item) => {
              me.columns.push(item.name)
            });
            me.reasonList = res.data
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
    },
    onCancel() {
      this.show = false;
    },
    delOrder(item) {
      this.$dialog
        .confirm({
          title: "温馨提示",
          message: "您确定要删除该订单吗？",
        })
        .then(() => {
          let me = this
        api.deleteOrder({id: item.id}).then(res => {
          if (res.code === 200) {
            me.$toast.success('删除成功');
          this.showOrder(me.activeName);
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
        })
        .catch(() => {
        });
    },
    sureRece(id) {
      this.$dialog
        .confirm({
          title: "温馨提示",
          message: "您确定要收货吗？",
        }).then(() => {
          let me = this
          api.orderConfirmReceiveOrder({id: id}).then(res => {
            if (res.code === 200) {
              this.showOrder(me.activeName)
              me.$toast.success('收货成功');
            } else {
              me.$notify({
                message: res.message,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }
          }).catch((res) => {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }).finally(() => {
          })
        }).catch(() => {
        });
    },
    toLogistics(muster){
      this.$router.push({
        path: "/user/logistics",
        query:{
          sw: JSON.stringify(muster)
        }
      });
    },
    toDetails(id) {
      this.$router.push({
        path: "/user/orderdetails",
        query:{id:id}
      });
    },
    nowPayment(item){
      let me = this
        if (item) {
          if (item.id) {
            me.data.filter(itm => {
              if (itm.orderSn === item.orderSn) {
                me.product = itm
              }
            })
          }
          me.$router.push({
            path:'/user/payment',
            query: {
              order: JSON.stringify(item),
              orderItemList : JSON.stringify(me.product.orderItemList[0])
            }
          })
        } else {
          me.$router.push({
            path:'/user/payment',
            query: {
              order: JSON.stringify(me.product),
              orderItemList : JSON.stringify(me.product.orderItemList[0])
            }
          })
        }
    }
  },
}
</script>

<style lang="stylus" scoped>
>>>.van-loading--vertical{
  margin-top 70%
}
>>>.van-tabs__nav--line.van-tabs__nav--complete {
  padding: 0px 0px 15px;
}
>>>.custom-image .van-empty__image {
    width: 90px;
    height: 90px;
}
.orderBody {
  width: 94.6%; // 355
  margin: 10px 2.6% 0px;
  padding-bottom: 20px;
  background: #fff;
  border-radius: 12px;

  .orderTop {
    width: 91.6%;
    height: 50px;
    margin: 0px 4.2%;
    border-bottom: 1px solid #f3f3f3;
    display: flex;
    justify-content: space-between;
    align-items: center;

    P {
      font-size: 14px;
      color: #666666;

      span {
        color: #000;
        font-weight: bold;
      }
    }
  }

  .orderCenter {
    width: 91.6%; // 325
    min-height: 86px;
    margin: 20px 4.2%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .orderimg{
      width: 26.4%;
      height: 86px;
      text-align center
      >>>.van-image {
      width: 86px;
      height: 86px;
    }
    }
    .orderfont {
      width: 69.2%;
      margin: 3px 0px;
      min-height: 80px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .ttop {
        overflow hidden
        // height  38px
        font-size: 15px;
        font-weight: bold;
        color: #000000;
        line-height: 21px;
      }
      .shopSpecs {
        display: flex;
        flex-wrap: wrap;
        font-size: 13px;
        margin-bottom: 5px;
        font-weight: 400;
        color: #888888;

        div{
          margin-right: 10px;
          margin-bottom: 5px;
        }
        }

      .bbottom {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left {
          font-size: 15px;
          color: #FD2744;

          span {
            font-size: 18px;
            font-weight: bold;
          }
        }
        .cancel{
          margin-left: 10px;
          color: #f22e00;
        }
        div {
          font-size: 12px;
          color: #666666;
        }
      }
    }
  }

  .orderBottom {
    width: 91.6%;
    margin: 20px 4.2% 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;

    .money {
      font-size: 13px;
      color: #999;

      span {
        font-weight: bold;
        font-size: 15px;
        color: #000;
      }
    }

    .btn {
      margin-top: 16px;
    }
  }
}
</style>


